
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'

import img1 from '../../assets/img/01.jpg'
import img2 from '../../assets/img/02.jpg'
import img3 from '../../assets/img/03.jpg'
export default{
data () {
return {
tabs: [
'详细信息',
'处理信息'
],
tabIndex: 0,
categoryIndex: 0,
list: [{
src: img1,
w: 800,
h: 400
},
{
src: img2
}, {
src: img3
}],
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
directives: {
TransferDom
},
activated () {
document.querySelector('#vux_view_box_body').scrollTop = 0
},
components: {
Tab,
TabItem,
Cell,
Group,
XTextarea,
CellBox,
GroupTitle,
Sticky,
TransferDom,
Previewer
},
computed: {
order () {
if (this.$store.state.orders) {
return this.$store.state.orders.filter(order => {
return order.id == this.$route.params.id
})[0]
}
},
orderManage () {
return this.order.manage.sort(function (a, b) {
return a.date < b.date ? 1 : -1
})
}
},
mounted () {
},
destroyed () {
},
methods: {
onCategoryClick (index) {
this.categoryIndex = index
console.log('category changed, current tab index is:', index)
},
showImg (index) {
this.$refs.previewer.show(index)
}
}
}



